/* link
/////////////////////////////////////////////////////////////////////////////////////// */
.gnavi{
    margin:0 0 50px 0;
    list-style: none;
}

.gnavi li a{
    display: block;
   padding:10px 30px;
    text-decoration: none;
    color: $base-color2;
}

.gnavi li{
    margin-bottom:20px;
}

/*==================================================
5-3-1 
===================================*/

.gnavi li a{
    /*relative*/
  position: relative;
}

.gnavi li.current a,
.gnavi li a:hover{
  color:$point-color;
}

.gnavi li a::after {
    content: '';
    /**/
    position: absolute;
    bottom: 0;
    left: 10%;
    /**/
    width: 80%;
    height: 2px;
    background:$point-color;
    /**/
    transition: all .3s;
    transform: scale(0, 1);/*X0Y1*/
    transform-origin: center top;/**/
}

/*hover*/
.gnavi li.current a::after,
.gnavi li a:hover::after {
    transform: scale(1, 1);/*X*/
}

