/*========= CSS ===============*/
:root {
  --bar-thickness: 5px; }

.hamburger_posi {
  /* 768px*/ }
  @media (max-width: 48em) {
    .hamburger_posi {
      position: relative;
      top: -35px;
      left: -40px; } }
  @media screen and (max-width: 320px) {
    .hamburger_posi {
      position: relative;
      top: -31px;
      left: -37px; } }

.hamburger {
  width: 30px;
  height: 25px;
  position: absolute;
  transition: transform .5s;
  transform: rotate(45deg);
  top: 50vh;
  left: 0;
  right: 0;
  margin: auto;
  /* 768px*/ }
  @media (max-width: 48em) {
    .hamburger {
      top: 0;
      left: 0px;
      right: auto; } }

.hamburger:hover {
  /*pc769px*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover {
      transform: rotate(-45deg);
      margin: auto;
      left: 0;
      right: 0; } }

.hamburger div {
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #FFF;
  border-radius: 3px;
  transform-origin: 50% 50%;
  transition: .5s .25s; }

.hamburger div:nth-child(1) {
  top: 0;
  width: 22px;
  left: 4px; }

.hamburger div:nth-child(2) {
  top: calc(50% - var(--bar-thickness) / 2);
  margin: auto;
  left: 0;
  right: 0; }

.hamburger div:nth-child(3) {
  top: calc(100% - var(--bar-thickness));
  width: 22px;
  left: 4px; }

.hamburger:hover div {
  /*pc769px*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div {
      top: calc(50% - var(--bar-thickness) / 2); } }

.hamburger:hover div:nth-child(1) {
  /*pc769px*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div:nth-child(1) {
      transform: scale(1, 0);
      transform-origin: 50% 50%;
      margin: auto;
      left: 0;
      right: 0; } }

.hamburger:hover div:nth-child(2) {
  /*pc769px*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div:nth-child(2) {
      top: calc(50% - var(--bar-thickness) / 2);
      width: 30px;
      margin: auto;
      left: 0;
      right: 0; } }

.hamburger:hover div:nth-child(3) {
  /*pc769px*/ }
  @media all and (min-width: 48.0625em) {
    .hamburger:hover div:nth-child(3) {
      transform: scale(1, 0);
      transform-origin: 50% 50%; } }

.hamburger.header_active {
  transform: rotate(-45deg);
  margin: auto; }

.hamburger.header_active div {
  top: calc(50% - var(--bar-thickness) / 2);
  margin: auto;
  left: 0;
  right: 0; }

.hamburger.header_active div:nth-child(1) {
  transform: rotate(45deg);
  width: 31px;
  transform: rotate(90deg);
  background-color: #1A8BAC;
  transition: .5s .25s;
  margin: auto;
  left: 0;
  right: 0; }

.hamburger.header_active div:nth-child(2) {
  top: calc(50% - var(--bar-thickness) / 2);
  width: 30px;
  margin: auto;
  left: 0;
  right: 0; }

.hamburger.header_active div:nth-child(3) {
  opacity: 0; }

